.tableheader {
  display: flex;
  justify-content: space-between;

  padding: 30px 40px;
}
.csv {
  display: flex;
  align-items: center;
  flex: 0.7;
  margin-right: 120px;
  justify-content: space-between;
}
.export {
  flex: 0.3;
}
.csvinput {
  font-size: 16px;
  background-color: white;
  border-radius: 50px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
    rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
  outline: none;
  padding: 10px 100% 10px 10px;
}
::-webkit-file-upload-button {
  color: white;
  background-color: #11493e;
  border: none;
  border-radius: 50px;
  box-shadow: rgba(0, 0, 0, 0.06) 0px 2px 4px 0px inset;
  padding: 10px 29px;
  outline: none;
}
.csvbutton {
  width: 30%;
}
