.rt-profile-card {
  width: 240px;
  background-color: #fff;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.1);
  background-position: center;
  overflow: hidden;
  position: relative;
  margin-right: 60px;
  cursor: pointer;
  border-radius: 10px;
  margin-bottom: 40px;
}

.rt-profile-card img.full-width {
  transition: all linear 0.25s;
  width: 300px;
  height: 400px;
  object-fit: contain;
}
.rt-profile-card img:hover {
  opacity: 0.2;
}

.rt-profile-card .rt-profile-body {
  position: absolute;
  left: 30px;
  right: 15px;
  bottom: 0;
  transition: all linear 0.25s;
}

.rt-profile-card .rt-profile-name {
  font-size: 30px;
  line-height: 1;
  color: #fff;
  text-shadow: 0px 0px 20px rgba(0, 0, 0, 0.5);
  font-weight: bold;
  position: relative;
  transition: all 400ms ease-out;
  text-transform: uppercase;
}

.rt-profile-card .rt-profile-text {
  visibility: hidden;
  opacity: 0;
  font-size: 16px;
  line-height: 1.4;
  color: #fff;
  text-shadow: 0px 0px 20px rgba(0, 0, 0, 0.5);
  margin-top: 10px;
  height: unset;
  transition: all linear 0.25s;
}

.rt-profile-card:hover .rt-profile-body {
  bottom: 30px;
}

.rt-profile-card:hover .rt-profile-name {
  color: #11493e;
}

.rt-profile-card .rt-profile-name::before,
.rt-profile-card .rt-profile-name::after {
  content: "";
  display: inline-block;
  background: #fff;
  border-radius: 20px;
  width: 120px;
  height: 2px;
  margin-right: 0;
  margin-left: 0;
  transition: all 250ms ease-in;
}

.rt-profile-card .rt-profile-name::before {
  position: absolute;
  left: 0;
  top: -7px;
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

.rt-profile-card .rt-profile-name::after {
  position: absolute;
  left: 0;
  bottom: -7px;
  -webkit-transform: translateX(-200px);
          transform: translateX(-200px);
}

.rt-profile-card:hover .rt-profile-name::before {
  content: "";
  background: #ffc920;
  -webkit-transform: translateX(-200px);
          transform: translateX(-200px);
}

.rt-profile-card:hover .rt-profile-name::after {
  content: "";
  background: #ffc920;
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

.rt-profile-card:hover .rt-profile-text {
  visibility: visible;
  opacity: 1;
  transition: all 400ms ease-out;
  height: auto;
  height: initial;
}

@media (max-width: 768px) {
  .rt-profile-card .rt-profile-body {
    bottom: 30px;
  }
  .rt-profile-card .rt-profile-text {
    visibility: visible;
    opacity: 1;
    height: auto;
    height: initial;
  }
  .rt-profile-card .rt-profile-name::before {
    display: none;
  }
  .rt-profile-card .rt-profile-name::after {
    position: absolute;
    left: 0;
    bottom: -7px;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

