:root {
  --main-color: #11493e;
  --color-dark: #11493e;
  /* --main-color: #11101d; */
  /* --color-dark: #1d2231; */
  --text-grey: #8390a2;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  font-family: "Poppins", sans-serif;
}

.sidebarr {
  width: 345px;
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  z-index: 100;
  background: #11493e;
  background: var(--main-color);
  transition: width 300ms;
}
.sidebar-brand {
  height: 90px;
  padding: 1rem 0rem 1rem 2rem;
  color: #fff;
}
.sidebar-brand span {
  display: inline-block;
  padding-right: 1rem;
}
.sidebar-menu li {
  width: 100%;
  margin-bottom: 1.7rem;
  padding-left: 1rem;
}
.sidebar-menu {
  margin-top: 1rem;
}
.sidebar-menu a {
  padding-left: 1rem;
  display: block;
  color: #fff;
  font-size: 1.1rem;
}
.nav-toggle:checked + .sidebarr {
  width: 70px;
}
.nav-toggle:checked + .sidebarr .sidebar-brand,
.nav-toggle:checked + .sidebarr li {
  padding-left: 1rem;
  text-align: center;
}
.nav-toggle:checked + .sidebarr li a {
  padding-left: 0rem;
}
.nav-toggle:checked + .sidebarr .sidebar-brand h1 span:last-child,
.nav-toggle:checked + .sidebarr li a span:last-child {
  display: none;
}
.sidebar-menu a span:first-child {
  font-size: 1.5rem;
  padding-right: 1rem;
}
.sidebar-menu a.active {
  background: #fff;
  padding-top: 1rem;
  padding-bottom: 1rem;
  color: #11493e;
  color: var(--main-color);
  border-radius: 30px 0px 0px 30px;
}
.nav-toggle:checked ~ .main-content {
  margin-left: 70px;
}
.nav-toggle:checked ~ .main-content header {
  width: calc(100% - 70px);
  left: 70px;
}
.main-content {
  transition: margin-left 300ms;
  margin-left: 345px;
}
header {
  background: #fff;
  display: flex;
  justify-content: space-between;
  padding: 1rem 1.5rem;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
  position: fixed;
  left: 345px;
  top: 0;
  z-index: 100;
  width: calc(100% - 345px);
  transition: left 300ms;
}

.nav-toggle {
  display: none;
}
header h2 {
  color: #222;
}
header label span {
  font-size: 1.7rem;
  padding-right: 1rem;
}
