.info small {
  font-weight: 600;
  color: #8390a2;
  color: var(--text-grey);
}
.contact span {
  font-size: 1.2rem;
  display: inline-block;
  margin-left: 0.5rem;
  color: #11493e;
  color: var(--main-color);
}

@media only screen and (max-width: 1200px) {
  .sidebarr {
    width: 70px;
  }
  .sidebarr .sidebar-brand,
  .sidebarr li {
    padding-left: 1rem;
    text-align: center;
  }
  .nav-toggle:checked + .sidebarr li a {
    padding-left: 0rem;
  }
  .sidebarr .sidebar-brand h1 span:last-child,
  .sidebarr li a span:last-child {
    display: none;
  }

  .main-content {
    margin-left: 70px;
  }
  .main-content header {
    width: calc(100% - 70px);
    left: 70px;
  }
}

@media only screen and (max-width: 960px) {
  .cards {
    grid-template-columns: repeat(3, 1fr);
  }
  .recent-grid {
    grid-template-columns: 60% 40%;
  }
}

@media only screen and (max-width: 768px) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
  .recent-grid {
    grid-template-columns: 100%;
  }
  .search-wrapper {
    display: none;
  }
  .sidebarr {
    left: -100% !important;
  }
  header h2 {
    display: flex;
    align-items: center;
  }
  header h2 label {
    display: inline-block;
    text-align: center;
    background: #11493e;
    background: var(--main-color);
    padding-right: 0rem;
    margin-right: 1rem;
    height: 40px;
    width: 40px;
    border-radius: 50%;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center !important;
  }
  header h2 span {
    text-align: center;
    padding-right: 0rem;
  }
  header h2 {
    font-size: 1.1rem;
  }
  .main-content {
    width: 100%;
    margin-left: 0rem;
  }
  header {
    width: 100% !important;
    left: 0 !important;
  }
  .nav-toggle:checked + .sidebarr {
    left: 0 !important;
    z-index: 100;
    width: 345px;
  }

  .nav-toggle:checked .sidebarr .sidebar-brand,
  .nav-toggle:checked + .sidebarr li {
    padding-left: 2rem;
    text-align: left;
  }

  .nav-toggle:checked + .sidebarr li a {
    padding-left: 1rem;
  }
  .nav-toggle:checked + .sidebarr .sidebar-brand h1 span:last-child,
  .nav-toggle:checked + .sidebarr li a span:last-child {
    display: inline;
  }
  .nav-toggle:checked ~ .main-content {
    margin-left: 0rem !important;
  }
}
@media only screen and (max-width: 560px) {
  .cards {
    grid-template-columns: 100%;
  }
}

